<template>
    <div class="loading-container" v-show="show">
        <div class="loading-mask"></div>
        <div class="loading-content">
            <div class="loading-animate"></div>
            <div class="loading-text">{{text}}</div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        show: {
            type: Boolean
        },
        text: {
            type: String,
            default: "加载中..."
        }
    }
};
</script>

<style>
.loading-container {
    position: relative;
    text-align: center;
    z-index: 999;
}
.loading-mask {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(255,255,255, 0.3);
}
.loading-content {
    position: fixed;
    left: 50%;
    top: 45%;
    z-index: 999;
    transform: translate(-50%, -45%);
    text-align: center;
    color: #fff;
}
.loading-content .loading-animate {
    display: inline-block;
    width: 40px;
    height: 40px;
    vertical-align: baseline;
    margin: 25px 0 10px;
    vertical-align: middle;
    animation: cricleLoading 1s steps(12, end) infinite;
    background: transparent
        url("data:image/svg+xml;base64,")
        no-repeat;
    background-size: 100%;
}
.loading-content .loading-text {
    font-size: 16px;
}
@-webkit-keyframes cricleLoading {
    0% {
        transform: rotate3d(0, 0, 1, 0deg);
    }
    100% {
        transform: rotate3d(0, 0, 1, 360deg);
    }
}
@keyframes cricleLoading {
    0% {
        transform: rotate3d(0, 0, 1, 0deg);
    }
    100% {
        transform: rotate3d(0, 0, 1, 360deg);
    }
}
</style>


